.pce-ac-tooltip{background:var(--widget__bg);border:1px solid var(--widget__border);pointer-events:auto;box-sizing:border-box;position:sticky;right:.5em;left:.5em;overflow:auto;border-radius:.3em;white-space:pre}.pce-ac-tooltip>ul{all:unset;display:block;box-sizing:border-box;overflow:hidden}.pce-ac-row{display:flex;padding:0 .2em;height:1.4em;align-items:center;gap:.5em;cursor:pointer}.pce-ac-row span{color:var(--pce-ac-match, #52b1ff)}.pce-ac-row div{overflow-x:hidden;text-overflow:ellipsis}.pce-ac-details{opacity:.75;font-size:90%;margin-left:auto}.pce-ac-details:empty{display:none}@media (hover: hover){.pce-ac-row:hover{background:#7772}}.pce-ac-row[aria-selected]{background:#1094ff36;color:var(--widget__color-active)}.pce-tabstops span{background:var(--pce-tabstop, #7c7c7c4d)}.pce-tabstops :empty{padding:0 1px;margin:0 -1px}
